// animate css

.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
}

.blind {
  visibility: hidden;
}

.fadeInRight,
.fadeInLeft,
.fadeInUp {
  visibility: visible;
}

@-webkit-keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
    transform: translateY(20px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
    -ms-transform: translateY(20px);
    transform: translateY(20px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    transform: translateY(0);
  }
}

.fadeInUp {
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp;
}

@keyframes fadeInLeft {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-20px);
    -ms-transform: translateX(-20px);
    transform: translateX(-20px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
    -ms-transform: translateX(0);
    transform: translateX(0);
  }
}

.fadeInLeft {
  -webkit-animation-name: fadeInLeft;
  animation-name: fadeInLeft;
}

@keyframes fadeInRight {
  0% {
    opacity: 0;
    -webkit-transform: translateX(20px);
    -ms-transform: translateX(20px);
    transform: translateX(20px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
    -ms-transform: translateX(0);
    transform: translateX(0);
  }
}

.fadeInRight {
  -webkit-animation-name: fadeInRight;
  animation-name: fadeInRight;
}

.pop-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: table;
  transition: opacity 0.3s ease;
}

.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: table;
  transition: opacity 0.3s ease;

  &.popup,
  &.scale-no {
    transition: initial;
  }

  &.bg-no {
    background-color: transparent !important;
  }
}

.pop-wrapper,
.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.pop-container {
  position: relative;
  margin: 0 auto;
}

.modal-container {
  position: relative;
  margin: 0 auto;
  transform: scale(1.1);
  transition: all 0.3s ease;
}

.pop-header,
.pop-body,
.pop-footer,
.modal-header,
.modal-body,
.modal-footer {
  position: relative;
}

.modal {
  transform: scale(1);
  opacity: 0;
  visibility: hidden;
}

.modal.enter {
  opacity: 1;
  visibility: visible;
}

.modal.enter .modal-container {
  transform: scale(1);
  -webkit-transform: scale(1);
}

.modal.leave {
  opacity: 0;
  visibility: hidden;
}

.modal.leave .modal-container {
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
}

.modal.popup .modal-container,
.modal.scale-no .modal-container,
.modal.leave.scale-no .modal-container {
  transform: scale(1);
  -webkit-transform: scale(1);
}

