// 글자 수 표시
.texta-wrapper {
  position: relative;

  .texta-counter {
    position: absolute;
    right: 23px;
    bottom: 2px;
    color: $font-gray4;
    font-size: 12px;
    font-weight: 300;
  }
}

// select 커스텀
.dropselect {
  display: inline-block;
  position: relative;

  select {
    position: absolute !important;
    bottom: 0;
    left: 50%;
    display: block !important;
    width: 0.5px !important;
    height: 100% !important;
    padding: 0 !important;
    opacity: 0 !important;
    border: none;
    z-index: 0 !important;
  }

  .dropbtn {
    @include btn-base;
    @include btn-size(5px 10px, 13px);
    // @include btn-transparent($admin-c-black);
    @include btn-transparent($admin-primary);
    color: $admin-body-c;
    width: 120px;
    text-align: left;
    border-radius: 3px;

    &:after {
      position: absolute;
      font-family: 'Material Design Icons';
      content: '\F140';
      font-size: 20px;
      top: 50%;
      transform: translateY(-50%);
      right: 10px;
      // vertical-align: middle;
    }
  }

  .dropmenu {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    padding: 10px 0;
    background-color: #fff;
    box-shadow: 0 0 50px 0 rgba(82, 63, 105, 0.18);
    border-radius: 3px;

    .item {
      padding: 10px;
      font-size: 13px;
      color: $admin-body-c;
      cursor: pointer;

      &:hover,
      &.active {
        background-color: #f7f8fa;
      }
    }

    &.active {
      display: block;
    }
  }
}

// fileform 커스텀
.fileform-wrapper {
  position: relative;

  input[type='file'] {
    position: absolute !important;
    bottom: 0;
    left: 50%;
    display: block !important;
    width: 0.5px !important;
    height: 100% !important;
    padding: 0 !important;
    opacity: 0 !important;
    border: none;
    z-index: 0 !important;
  }

  > * {
    vertical-align: middle;
  }

  label {
    @include btn-base;
    @include btn-size(5px 10px, 13px);
    @include btn-transparent($admin-c-black);
    // @include btn-transparent($admin-primary);
    color: $admin-body-c;
    min-width: 100px;
    text-align: left;
    border-radius: 3px;

    &:after {
      position: absolute;
      font-family: 'Material Design Icons';
      content: '\FA4D';
      font-size: 20px;
      top: 50%;
      transform: translateY(-50%);
      right: 6px;
      margin-top: 1px;
    }
  }

  .filename {
    display: inline-block;
    margin-left: 5px;
    font-size: 13px;
    color: $admin-body-c;
  }

  &.pd-md {
    label {
      padding: 9px 15px;
      min-width: 125px;

      &:after {
        right: 8px;
      }
    }

    .filename {
      margin-left: 7px;
    }
  }
}

// 에러 페이지
#errorPage {
  .header,
  .footer {
    display: none;
  }
}

.err-mask {
  background: none;
}

.err-wrapper {
  text-align: center;

  .err-container {
    font-size: 16px;
    width: 60%;
    min-width: 310px;
    padding: 100px 50px;
    box-sizing: border-box;
    border-radius: 20px;
    background-color: #fff;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
  }

  .title {
    font-size: 10em;
    font-weight: 800;
    margin-bottom: 50px;
  }

  .st {
    font-size: 3em;
  }

  .text {
    margin-top: 30px;
    font-size: 0.9em;
    color: $c-red;
  }

  .err-actions {
    margin-top: 30px;
  }

  .home-btn {
    @include btn-base;
    @include btn-size(15px 90px, 18px);
    @include btn-transparent(#141414);
    border-width: 2px;
    border-radius: 0;
  }
}

// custom
.tbn-default.product-info-tbl {
  letter-spacing: -1px;
}

@media (max-width: 991px) {
  .comm-title {
    @include comm-title-xs;
  }

  .comm-text {
    @include comm-text-xs;
  }

  .tbl-default {
    @include tbl-base-xs;
  }

  .btn.custom-btn,
  .btn.product-more-btn {
    @include btn-size(5px 8px, 12px);
    letter-spacing: -0.5px;

    .gl {
      height: 18px;
      margin-right: 2px;
    }

    background-color: #f1f1f1;
    border-color: #b5b5b5;
    border-radius: 0;
  }

  .dot-square-default {
    @include dot-square(10px, 7px);
  }
}

@media (min-width: 992px) {
  .comm-title {
    @include comm-title;
  }

  .comm-text {
    @include comm-text;
  }

  .tbl-default {
    @include tbl-base;
  }

  .btn.custom-btn,
  .btn.product-more-btn {
    @include btn-size(10px 15px, 16px);
    letter-spacing: -0.5px;

    .gl {
      height: 24px;
      margin-right: 5px;
    }

    background-color: #f1f1f1;
    border-color: #b5b5b5;
    border-radius: 0;
  }

  .dot-square-default {
    @include dot-square;
  }
}
